/* ZIP CODE INPUT */

.red-warning {
  box-shadow: inset 0 0 6px #ff0000;
}

.zip-shake {
  -webkit-animation: zipshake 3.72s ease infinite;
          animation: zipshake 3.72s ease infinite;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

@-webkit-keyframes zipshake {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0)
  }

  1.78571% {
    -webkit-transform: translate(5px, 0);
            transform: translate(5px, 0)
  }

  3.57143% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0)
  }

  5.35714% {
    -webkit-transform: translate(5px, 0);
            transform: translate(5px, 0)
  }

  7.14286% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0)
  }

  8.92857% {
    -webkit-transform: translate(5px, 0);
            transform: translate(5px, 0)
  }

  10.71429% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0)
  }

  100% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0)
  }
}

@keyframes zipshake {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0)
  }

  1.78571% {
    -webkit-transform: translate(5px, 0);
            transform: translate(5px, 0)
  }

  3.57143% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0)
  }

  5.35714% {
    -webkit-transform: translate(5px, 0);
            transform: translate(5px, 0)
  }

  7.14286% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0)
  }

  8.92857% {
    -webkit-transform: translate(5px, 0);
            transform: translate(5px, 0)
  }

  10.71429% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0)
  }

  100% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0)
  }
}

