/* Eight ball image built by Justin Khoo,
you can find the original work on codepen here https://codepen.io/freshinbox/pen/VQpQyo */

.eight-ball {
  visibility: hidden;
  background-image: url(https://marketing.emailonacid.com/hubfs/2017-template-images/8ball/Magic8BallEmail_EmptyBall.png);
  background-size: cover;
  position: absolute;
  bottom: 0;
  width: 500px;
  height: 300px;
  z-index: 3;
  background-repeat: no-repeat;
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
  --webkit-filter: drop-shadow(1px 1px 0.75rem rgb(15, 15, 15));
  -webkit-filter: drop-shadow(1px 1px 0.75rem rgb(15, 15, 15));
          filter: drop-shadow(1px 1px 0.75rem rgb(15, 15, 15));
}

.rise {
  -webkit-animation: rise .3s linear;
          animation: rise .3s linear;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.hide {
  visibility: hidden;
  -webkit-animation: lower .2s linear;
          animation: lower .2s linear;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes rise {
  0% {
    /* visibility: visible; */
  }
  100% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
    visibility: visible;
  }
}

@keyframes rise {
  0% {
    /* visibility: visible; */
  }
  100% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
    visibility: visible;
  }
}

.lower {
  -webkit-animation: lower .2s linear;
          animation: lower .2s linear;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes lower {
  0% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
    visibility: visible;
  }
  100% {
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
    visibility: hidden;
  }
}

@keyframes lower {
  0% {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
    visibility: visible;
  }
  100% {
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
    visibility: hidden;
  }
}

.shake {
  -webkit-animation: shake 1.5s forwards;
          animation: shake 1.5s forwards;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
}

@-webkit-keyframes shake {
  0% {
    -webkit-transform: translate(1px, 2%) rotate(0deg);
            transform: translate(1px, 2%) rotate(0deg);
    visibility: visible;
  }
  10% {
    -webkit-transform: translate(-1px, 1%) rotate(-1deg);
            transform: translate(-1px, 1%) rotate(-1deg);
  }
  20% {
    -webkit-transform: translate(-3px, 2%) rotate(1deg);
            transform: translate(-3px, 2%) rotate(1deg);
  }
  30% {
    -webkit-transform: translate(3px, 1%) rotate(0deg);
            transform: translate(3px, 1%) rotate(0deg);
  }
  40% {
    -webkit-transform: translate(1px, 2%) rotate(1deg);
            transform: translate(1px, 2%) rotate(1deg);
  }
  50% {
    -webkit-transform: translate(-1px, 2%) rotate(-1deg);
            transform: translate(-1px, 2%) rotate(-1deg);
  }
  60% {
    -webkit-transform: translate(-3px, 2%) rotate(0deg);
            transform: translate(-3px, 2%) rotate(0deg);
  }
  70% {
    -webkit-transform: translate(3px, 1%) rotate(-1deg);
            transform: translate(3px, 1%) rotate(-1deg);
  }
  80% {
    -webkit-transform: translate(-1px, 2%) rotate(1deg);
            transform: translate(-1px, 2%) rotate(1deg);
  }
  90% {
    -webkit-transform: translate(1px, 1%) rotate(0deg);
            transform: translate(1px, 1%) rotate(0deg);
  }
  100% {
    -webkit-transform: translate(0px, 1%) rotate(-1deg);
            transform: translate(0px, 1%) rotate(-1deg);
    visibility: visible;
  }
}

@keyframes shake {
  0% {
    -webkit-transform: translate(1px, 2%) rotate(0deg);
            transform: translate(1px, 2%) rotate(0deg);
    visibility: visible;
  }
  10% {
    -webkit-transform: translate(-1px, 1%) rotate(-1deg);
            transform: translate(-1px, 1%) rotate(-1deg);
  }
  20% {
    -webkit-transform: translate(-3px, 2%) rotate(1deg);
            transform: translate(-3px, 2%) rotate(1deg);
  }
  30% {
    -webkit-transform: translate(3px, 1%) rotate(0deg);
            transform: translate(3px, 1%) rotate(0deg);
  }
  40% {
    -webkit-transform: translate(1px, 2%) rotate(1deg);
            transform: translate(1px, 2%) rotate(1deg);
  }
  50% {
    -webkit-transform: translate(-1px, 2%) rotate(-1deg);
            transform: translate(-1px, 2%) rotate(-1deg);
  }
  60% {
    -webkit-transform: translate(-3px, 2%) rotate(0deg);
            transform: translate(-3px, 2%) rotate(0deg);
  }
  70% {
    -webkit-transform: translate(3px, 1%) rotate(-1deg);
            transform: translate(3px, 1%) rotate(-1deg);
  }
  80% {
    -webkit-transform: translate(-1px, 2%) rotate(1deg);
            transform: translate(-1px, 2%) rotate(1deg);
  }
  90% {
    -webkit-transform: translate(1px, 1%) rotate(0deg);
            transform: translate(1px, 1%) rotate(0deg);
  }
  100% {
    -webkit-transform: translate(0px, 1%) rotate(-1deg);
            transform: translate(0px, 1%) rotate(-1deg);
    visibility: visible;
  }
}

