.wrap {
  position: relative;
  width: 100%;
  height: 100%;
}

canvas {
  z-index: -1000;
}

canvas, .ateball {
  position: absolute;
}

.ateball-title {
  background-color: transparent;
  position: absolute;
  margin-top: 100px;
  opacity: 0;
  top: 25%;
  left: 50%;
  transform: translateX(-50%);
  color: white;
  text-align: center;
}

.ateball-title-show {
  opacity: 1;
  transition: 1s linear;
}

/* Eight ball image built by Justin Khoo,
you can find the original work on codepen here https://codepen.io/freshinbox/pen/VQpQyo */

.ateball {
  background-image: url(https://marketing.emailonacid.com/hubfs/2017-template-images/8ball/Magic8BallEmail_EmptyBall.png);
  background-size: cover;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 500px;
  height: 500px;
  z-index: 1000;
  background-repeat: no-repeat;
  transform: translateY(200%) translateX(-50%);
  --webkit-filter: drop-shadow(1px 1px 0.75rem rgb(15, 15, 15));
  filter: drop-shadow(1px 1px 0.75rem rgb(15, 15, 15));
  cursor: pointer;
  padding-top: 10px;
}

.ateball-rise {
  animation: ateball-rise 1s linear 1s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}

@keyframes ateball-rise {
  0% {
    opacity: 1;
  }
  100% {
    transform: translateY(50%) translateX(-50%);
    opacity: 1;
  }
}

.ateball-shake {
  animation: ateball-shake 2s forwards;
  animation-iteration-count: 1;
}

@keyframes ateball-shake {
  0% {
    transform: translateY(49%) translateX(-51%) rotate(0deg);
    opacity: 1;
  }
  10% {
    transform: translateY(49%) translateX(-52%) rotate(-1deg);
  }
  20% {
    transform: translateY(47%) translateX(-53%) rotate(1deg);
  }
  30% {
    transform: translateY(48%) translateX(-52%) rotate(0deg);
  }
  40% {
    transform: translateY(49%) translateX(-51%) rotate(1deg);
  }
  50% {
    transform: translateY(50%) translateX(-53%) rotate(-1deg);
  }
  60% {
    transform: translateY(49%) translateX(-52%) rotate(0deg);
  }
  70% {
    transform: translateY(48%) translateX(-51%) rotate(-1deg);
  }
  80% {
    transform: translateY(50%) translateX(-50%) rotate(1deg);
  }
  90% {
    transform: translateY(49%) translateX(-51%) rotate(0deg);
  }
  100% {
    transform: translateY(50%) translateX(-50%) rotate(-1deg);
    opacity: 1;
  }
}

.ateball-triangle {
  background-image: url(https://marketing.emailonacid.com/hubfs/2017-template-images/8ball/Magic8BallEmail_Triangle1.png);
  background-size: cover;
  width: 35%;
  height: 31%;
  position: absolute;
  top: 55%;
  left: 50%;
  background-repeat: no-repeat;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%) translateY(-50%);
  color: white;
  opacity: 0;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.ateball-triangle-show {
  animation: ateball-triangle-show .3s linear 1.6s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}

@keyframes ateball-triangle-show {
  100% {
    opacity: .6;
  }
}

.ateball-triangle a {
  word-spacing: 9999999px;
  text-align: center;
  font-size: 17px;
  color: white;
  text-decoration: none;
  text-shadow: .5px .5px 7px rgba(255, 255, 255, 0.69);
}

.ateball-triangle a:visited, .ateball-triangle a:hover, .ateball-triangle a:active {
  color: white;
  text-decoration: none;
  text-shadow: 2px 2px 14px rgba(255, 255, 255, 0.69);
}

@media screen and (max-width: 600px) {
  .ateball {
    width: 300px;
    height: 300px;
    transform: translateY(300%) translateX(-50%);
  }
  .ateball-rise {
    animation: ateball-rise 1s linear 1s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
  }
  @keyframes ateball-rise {
    0% {
      opacity: 1;
    }
    100% {
      transform: translateY(120%) translateX(-50%);
      opacity: 1;
    }
  }
  @keyframes ateball-shake {
    0% {
      transform: translateY(119%) translateX(-51%) rotate(0deg);
      opacity: 1;
    }
    10% {
      transform: translateY(119%) translateX(-52%) rotate(-1deg);
    }
    20% {
      transform: translateY(117%) translateX(-53%) rotate(1deg);
    }
    30% {
      transform: translateY(118%) translateX(-52%) rotate(0deg);
    }
    40% {
      transform: translateY(119%) translateX(-51%) rotate(1deg);
    }
    50% {
      transform: translateY(120%) translateX(-53%) rotate(-1deg);
    }
    60% {
      transform: translateY(119%) translateX(-52%) rotate(0deg);
    }
    70% {
      transform: translateY(118%) translateX(-51%) rotate(-1deg);
    }
    80% {
      transform: translateY(120%) translateX(-50%) rotate(1deg);
    }
    90% {
      transform: translateY(119%) translateX(-51%) rotate(0deg);
    }
    100% {
      transform: translateY(120%) translateX(-50%) rotate(-1deg);
      opacity: 1;
    }
  }
  .ateball-triangle {
    width: 35%;
    height: 31%;
    padding-top: 2px;
  }
  .ateball-triangle a {
    font-size: 1.2rem;
  }
}

/**
 * modal for restaurant
 */

#modal-disappear {
  visibility: hidden;
}

#modal-appear {
  visibility: visible;
}

.modal {
  background-color: rgba(0, 0, 0, .6);
}

.modal-content {
  /* top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(45%); */
  width: 100%;
  height: 100%;
  background-color: white;
  border-radius: 10px;
}

.modalBox {
  padding: 0;
}

.modalBox {
  display: flex;
  flex-direction: column;
  list-style: none;
  justify-content: center;
  align-items: center;
}

.modalBox--list {
  font-size: 25px;
}

.modalBox--div {
  display: flex;
  justify-content: center;
  width: 100%;
}

.modalBox--image {
  height: 150px;
  width: 150px;
  border-radius: 50%;
}

.modalHeader {
  text-align: center !important;
}

.red-button-yelp {
  padding: 8px 17px;
  font-size: 2rem;
  border-radius: 4px;
  margin: 5px;
  margin-top: 20px;
  background-color: rgb(240, 62, 62);
  color: white;
  transition: .2s;
}

.modal--footer {
  display: flex;
  align-items: center;
  justify-content: center;
}

.open {
  width: 100px;
  background-color: rgba(0, 0, 0, .5);
  margin-top: 10px;
  border-radius: 5px;
}

.website {
  color: #333;
  font-size: 25px;
}

.website:hover {
  color: rgb(240, 62, 62);
}